.clearfix {
  clear: both;
  list-style: none !important;
  margin: 0; }

.text-center {
  text-align: center; }

.text-right {
  text-align: right; }

.text-left {
  text-align: left; }

.close {
  float: right;
  font-size: 21px;
  font-weight: bold;
  line-height: 1;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  filter: alpha(opacity=20);
  opacity: .2; }
  .close:hover, .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
    filter: alpha(opacity=50);
    opacity: .5; }

button.close {
  -webkit-appearance: none;
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: 0; }

object[type="application/gas-events-cef"],
iframe[name="google_conversion_frame"] {
  display: none !important;
  height: 0 !important;
  width: 0 !important;
  line-height: 0 !important;
  font-size: 0 !important;
  margin-top: -13px;
  float: left; }

.btn {
  display: inline-block;
  padding: .7em 1em;
  font-size: 14px;
  background-image: -webkit-linear-gradient(top, #e1e1e1 0%, #ccc 100%);
  background-image: -o-linear-gradient(top, #e1e1e1 0%, #ccc 100%);
  background-image: linear-gradient(to bottom, #e1e1e1 0%, #ccc 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#FFE1E1E1', endColorstr='#FFCCCCCC', GradientType=0);
  color: #333;
  border: none;
  text-decoration: none;
  border-radius: 8px;
  -moz-border-radius: 8px;
  -webkit-border-radius: 8px;
  -webkit-transition: 0.2s linear;
  -o-transition: 0.2s linear;
  transition: 0.2s linear; }
  .btn:hover {
    background-image: -webkit-linear-gradient(top, #d4d4d4 0%, #bfbfbf 100%);
    background-image: -o-linear-gradient(top, #d4d4d4 0%, #bfbfbf 100%);
    background-image: linear-gradient(to bottom, #d4d4d4 0%, #bfbfbf 100%);
    background-repeat: repeat-x;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#FFD4D4D4', endColorstr='#FFBFBFBF', GradientType=0);
    -webkit-transition: 0.2s linear;
    -o-transition: 0.2s linear;
    transition: 0.2s linear; }
  .btn[disabled] {
    cursor: default;
    opacity: 0.6;
    filter: alpha(opacity=60); }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #666; }

.container {
  position: relative; }
  .container:before, .container:after {
    content: " ";
    display: table; }
  .container:after {
    clear: both; }

.content-text {
  font-size: 14px;
  line-height: 1.4em; }
  .content-text p {
    margin: 0 0 25px; }
    .content-text p:last-child {
      margin-bottom: 0; }

.header {
  text-align: center; }

.footer {
  text-align: center; }
